/*global*/
html,
body {
  height: 100%;
}

img {
  max-width: 100%;
  object-fit: contain;
}

a {
  text-decoration: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: bold;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

/*index.html*/
.page {
  height: 100%;
}

.page__body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: #333333;
  margin: 0;
}
@media (min-width: 768px) {
  .page__body {
    font-size: 16px;
    line-height: 24px;
  }
}

.wrapper {
  min-width: 320px;
  max-width: 400px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .wrapper {
    max-width: 1440px;
  }
}
@media (min-width: 1440px) {
  .wrapper {
    padding: 0 40px;
  }
}

.button {
  display: flex;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  justify-content: center;
  text-align: center;
  margin: auto 0;
  padding: 8px 20px;
  border: none;
  border-radius: 4px;
  background-color: transparent;
  cursor: pointer;
}
.button:hover, .button:focus {
  color: #ffffff;
  background-color: #9070ec;
}
.button:active {
  color: #ffffff;
  background-color: #593cac;
}
.button--primary {
  color: #ffffff;
  background-color: #7859cf;
}
.button--primary:disabled {
  background-color: #bdbdbd;
}
.button--secondary {
  width: 100%;
  color: #7859cf;
  background-color: #ffffff;
}
.button--secondary:disabled {
  color: #ffffff;
  background-color: #e2e2e2;
}
@media (min-width: 768px) {
  .button--secondary {
    width: auto;
  }
}
@media (min-width: 768px) {
  .button--secondary {
    width: 100%;
  }
}

.button__disabled--primary {
  pointer-events: none;
  background-color: #bdbdbd;
}
.button__disabled--secondary {
  pointer-events: none;
  background-color: #e2e2e2;
}

.header {
  min-width: 320px;
}

.header__wrapper {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: 0;
}
@media (min-width: 768px) {
  .header__wrapper {
    padding: 0 20px;
  }
}
@media (min-width: 1440px) {
  .header__wrapper {
    padding-left: 40px;
    padding-right: 40px;
    justify-content: flex-end;
  }
}

.header__logo {
  margin: 0 10px 0 20px;
  order: 0;
}
@media (min-width: 768px) {
  .header__logo {
    margin: 0;
  }
}
@media (min-width: 1440px) {
  .header__logo {
    margin-right: auto;
  }
}

.header__menu {
  position: absolute;
  top: 100%;
  width: 100%;
  flex-grow: 1;
  background-color: #ffffff;
  z-index: 3;
  order: 2;
}
.header__menu--closed {
  display: none;
}
.header__menu--opened {
  display: block;
}
.header__menu--nojs {
  position: static;
}
@media (min-width: 768px) {
  .header__menu {
    display: block;
    position: static;
    flex-grow: 0;
    width: auto;
    order: 1;
  }
}
@media (min-width: 1440px) {
  .header__menu {
    margin-right: auto;
  }
}

.header__toggle {
  min-width: 60px;
  min-height: 60px;
  padding: 0;
  border: none;
  border-radius: 0;
  background-image: url("../img/header-menu-open.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-color: #ffffff;
  cursor: pointer;
  flex-shrink: 0;
  order: 3;
}
.header__toggle:active {
  background-color: #f0ebff;
}
.header__toggle--opened {
  background-image: url("../img/header-menu-close.svg");
}
.header__toggle--nojs {
  display: none;
}
@media (min-width: 768px) {
  .header__toggle {
    display: none;
  }
}

.header__user {
  margin-left: auto;
  order: 1;
}
@media (min-width: 768px) {
  .header__user {
    margin-left: 0;
    order: 2;
  }
}
@media (min-width: 1440px) {
  .header__user {
    margin-left: 18px;
  }
}

.header-logo {
  display: block;
}

@media (min-width: 1440px) {
  .header-logo__link {
    display: flex;
  }
}

.header-logo__image {
  display: block;
}
@media (min-width: 1440px) {
  .header-logo__image {
    margin-right: 20px;
  }
}

.header-logo__text {
  display: none;
}
@media (min-width: 1440px) {
  .header-logo__text {
    display: block;
    font-size: 12px;
    line-height: 16px;
    max-width: 140px;
    margin: auto 0;
    color: #333333;
  }
}

.user-nav {
  display: block;
}

.user-nav__list {
  display: flex;
  flex-wrap: wrap;
}

.user-nav__item {
  min-width: 60px;
  min-height: 60px;
  text-align: center;
}
@media (min-width: 1440px) {
  .user-nav__item {
    margin-right: 20px;
  }
  .user-nav__item:last-child {
    margin-right: 0;
  }
}

.user-nav__link {
  padding: 22px;
  display: block;
  color: #333333;
}
.user-nav__link:active {
  background-color: #f0ebff;
}
@media (min-width: 1440px) {
  .user-nav__link {
    display: flex;
  }
  .user-nav__link:hover, .user-nav__link:focus {
    color: #7859cf;
  }
  .user-nav__link:active {
    color: #7859cf;
  }
}

.user-nav__icon {
  width: 16px;
  height: 16px;
  display: block;
  margin: auto 0;
}
@media (min-width: 1440px) {
  .user-nav__icon {
    margin-right: 8px;
  }
}

.user-nav__text {
  display: none;
}
@media (min-width: 1440px) {
  .user-nav__text {
    display: block;
    font-size: 12px;
    line-height: 16px;
    font-weight: 500;
    text-transform: uppercase;
    margin: auto 0;
  }
}

@media (min-width: 768px) {
  .menu-nav__list {
    display: flex;
    flex-wrap: wrap;
  }
}

.menu-nav__item {
  text-align: center;
  border-top: 1px solid #f0ebff;
}
.menu-nav__item:last-child {
  border-bottom: 1px solid #f0ebff;
}
@media (min-width: 768px) {
  .menu-nav__item:last-child {
    border: none;
  }
}
@media (min-width: 768px) {
  .menu-nav__item {
    border: none;
  }
}
@media (min-width: 1440px) {
  .menu-nav__item {
    margin-right: 20px;
  }
  .menu-nav__item:last-child {
    margin-right: 0;
  }
}

.menu-nav__link {
  display: block;
  position: relative;
  min-height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  padding: 20px 32px;
  color: #333333;
  text-transform: uppercase;
}
.menu-nav__link--current {
  color: #7859cf;
}
.menu-nav__link:hover, .menu-nav__link:focus {
  color: #7859cf;
}
.menu-nav__link:active {
  color: #333333;
  background-color: #f0ebff;
}
@media (min-width: 768px) {
  .menu-nav__link {
    padding: 32px 20px;
  }
  .menu-nav__link--current::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    bottom: 0;
    background-color: #7859cf;
  }
  .menu-nav__link:active {
    background-color: #f0ebff;
  }
}

.main {
  flex: 1 0 auto;
}

.main__wrapper {
  background-color: #f8f8f8;
}

.section-title {
  display: block;
  font-size: 24px;
  line-height: 36px;
  font-weight: 700;
  text-align: center;
  margin: 0;
}
@media (min-width: 768px) {
  .section-title {
    font-size: 42px;
    line-height: 64px;
  }
}

.section-subtitle {
  font-size: 18px;
  line-height: 32px;
  font-weight: 700;
  margin: 0;
}
@media (min-width: 768px) {
  .section-subtitle {
    font-size: 24px;
    line-height: 36px;
  }
}

.badge {
  display: block;
  font-size: 12px;
  line-height: 14px;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  color: #7859cf;
  letter-spacing: 0.04rem;
}
@media (min-width: 768px) {
  .badge {
    line-height: 16px;
  }
}

.slider-swiper {
  position: relative;
}

.slider-swiper__pagination {
  display: none;
}
@media (min-width: 1440px) {
  .slider-swiper__pagination {
    position: absolute;
    display: flex;
    justify-content: center;
    z-index: 1;
    top: 90%;
  }
}

.slider-swiper__pagination span {
  width: 62px;
  height: 4px;
  border-radius: 0;
  background: none;
  opacity: 1;
  background-color: #ffffff;
}
.slider-swiper__pagination span:hover {
  background-color: rgba(120, 89, 207, 0.25);
}
.slider-swiper__pagination span:active {
  background-color: #7859cf;
}

.slider-swiper__pagination .swiper-pagination-bullet-active {
  background-color: #7859cf;
}

.slider-swiper__navigation.swiper-button-prev,
.slider-swiper__navigation.swiper-button-next {
  top: 0;
  height: 100%;
  margin-top: 0;
  width: 40px;
}
@media (min-width: 768px) {
  .slider-swiper__navigation.swiper-button-prev,
.slider-swiper__navigation.swiper-button-next {
    width: 80px;
    height: 76%;
  }
}
@media (min-width: 768px) {
  .slider-swiper__navigation.swiper-button-prev,
.slider-swiper__navigation.swiper-button-next {
    height: 100%;
  }
}
.slider-swiper__navigation.swiper-button-prev::after,
.slider-swiper__navigation.swiper-button-next::after {
  color: #7859cf;
  font-size: 20px;
}
.slider-swiper__navigation.swiper-button-prev.swiper-button-disabled,
.slider-swiper__navigation.swiper-button-next.swiper-button-disabled {
  color: #bdbdbd;
}

.slider-swiper__navigation.swiper-button-prev {
  left: 0;
  justify-content: center;
  padding-left: 3px;
  cursor: pointer;
}
.slider-swiper__navigation.swiper-button-prev:hover, .slider-swiper__navigation.swiper-button-prev:focus {
  background: linear-gradient(to right, #ffffff, transparent);
}
.slider-swiper__navigation.swiper-button-prev:active {
  background: linear-gradient(to right, rgba(120, 89, 207, 0.25), transparent);
}

.slider-swiper__navigation.swiper-button-next {
  right: 0;
  justify-content: flex-end;
  padding-right: 3px;
}
@media (min-width: 768px) {
  .slider-swiper__navigation.swiper-button-next {
    justify-content: center;
  }
}
.slider-swiper__navigation.swiper-button-next:hover, .slider-swiper__navigation.swiper-button-next:focus {
  background: linear-gradient(to left, #ffffff, transparent);
}
.slider-swiper__navigation.swiper-button-next:active {
  background: linear-gradient(to left, rgba(120, 89, 207, 0.25), transparent);
}

.slide {
  text-align: center;
  padding: 20px 20px 60px;
}
.slide--1 {
  background-color: #f3ebe1;
}
@media (min-width: 768px) {
  .slide--1 {
    background: linear-gradient(#f3ebe1 640px, #ffffff 640px);
  }
}
@media (min-width: 1440px) {
  .slide--1 {
    background: none;
    background-color: #f3ebe1;
  }
}
.slide--2 {
  background-color: #eae6fc;
}
@media (min-width: 768px) {
  .slide--2 {
    background: linear-gradient(#eae6fc 640px, #ffffff 640px);
  }
}
@media (min-width: 1440px) {
  .slide--2 {
    background: none;
    background-color: #eae6fc;
  }
}
.slide--3 {
  background-color: #e5e6e8;
}
@media (min-width: 768px) {
  .slide--3 {
    background: linear-gradient(#e5e6e8 640px, #ffffff 640px);
  }
}
@media (min-width: 1440px) {
  .slide--3 {
    background: none;
    background-color: #e5e6e8;
  }
}
@media (min-width: 768px) {
  .slide {
    display: flex;
    flex-direction: column;
    padding-top: 60px;
    padding-bottom: 0;
  }
}
@media (min-width: 1440px) {
  .slide {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;
    padding-top: 30px;
    padding-bottom: 65px;
  }
}

@media (min-width: 1440px) {
  .slide__wrapper {
    text-align: left;
    margin: 150px 0 auto;
  }
}

.slide__image {
  display: block;
  height: 100%;
  width: 280px;
  margin: 0 auto 6px;
}
@media (min-width: 768px) {
  .slide__image {
    width: 476px;
    order: 1;
    margin-bottom: 11px;
  }
}
@media (min-width: 1440px) {
  .slide__image {
    width: 600px;
    order: 0;
    margin-left: 85px;
  }
}

.slide__title {
  font-size: 36px;
  line-height: 48px;
  font-weight: bold;
  margin: 6px 0 9px 0;
  letter-spacing: 0.04rem;
}
@media (min-width: 768px) {
  .slide__title {
    font-size: 56px;
    line-height: 80px;
    margin-top: 5px;
    margin-bottom: 15px;
    letter-spacing: 0.07rem;
  }
}
@media (min-width: 1440px) {
  .slide__title {
    margin-top: 0;
    margin-bottom: 16px;
  }
}

.slide__text {
  max-width: 400px;
  margin: 0 auto 20px auto;
}
@media (min-width: 768px) {
  .slide__text {
    max-width: 1440px;
    font-size: 20px;
    line-height: 32px;
    margin: 0 80px 20px 80px;
  }
}
@media (min-width: 1440px) {
  .slide__text {
    max-width: 625px;
    margin: 0 0 40px 0;
  }
}

.slide__order {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .slide__order {
    margin-bottom: 20px;
  }
}
@media (min-width: 1440px) {
  .slide__order {
    margin: 0;
  }
}

@media (min-width: 1440px) {
  .slide__badge {
    text-align: left;
    margin-bottom: 4px;
  }
}

.order {
  display: flex;
  max-width: 280px;
  justify-content: center;
}
@media (min-width: 1440px) {
  .order {
    justify-content: flex-start;
  }
}

.order__price {
  display: flex;
  flex-wrap: wrap;
  margin: auto 0;
  line-height: 32px;
}
.order__price--old {
  font-size: 16px;
  color: #7859cf;
  text-decoration: line-through;
  margin-right: 6px;
}
.order__price--new {
  font-size: 20px;
  font-weight: 700;
}

.order__button {
  margin-left: 20px;
}

.features {
  padding: 50px 0 60px 0;
  text-align: center;
}
@media (min-width: 768px) {
  .features {
    padding: 42px 0 66px 0;
  }
}
@media (min-width: 1440px) {
  .features {
    padding: 101px 0 85px;
  }
}

.features__title {
  margin: 7px 0 36px 0;
}
@media (min-width: 768px) {
  .features__title {
    margin-top: 4px;
    margin-bottom: 61px;
  }
}
@media (min-width: 1440px) {
  .features__title {
    margin-bottom: 80px;
  }
}

.features__subtitle {
  margin-bottom: 4px;
}
@media (min-width: 768px) {
  .features__subtitle {
    margin-bottom: 10px;
    text-align: left;
  }
}
@media (min-width: 1440px) {
  .features__subtitle {
    text-align: center;
  }
}

@media (min-width: 768px) {
  .features__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;
    row-gap: 17px;
    padding: 0 10px;
  }
}
@media (min-width: 1440px) {
  .features__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 30px;
    row-gap: 50px;
    padding: 0;
  }
}

.features__item {
  position: relative;
  text-align: center;
  margin-bottom: 33px;
}
.features__item:last-child {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .features__item {
    padding-left: 72px;
  }
}
@media (min-width: 1440px) {
  .features__item {
    padding: 0;
  }
}
.features__item::before {
  content: "";
  display: inline-block;
  width: 52px;
  height: 56px;
  background-repeat: no-repeat;
  background-size: contain;
  margin-bottom: 4px;
}
@media (min-width: 768px) {
  .features__item::before {
    position: absolute;
    left: 0;
  }
}
@media (min-width: 1440px) {
  .features__item::before {
    position: static;
    margin-bottom: 14px;
  }
}
.features__item:nth-child(1)::before {
  background-image: url("../img/features-speed-1.png");
}
.features__item:nth-child(2)::before {
  background-image: url("../img/features-comfort-2.png");
}
.features__item:nth-child(3)::before {
  background-image: url("../img/features-energy-3.png");
}
.features__item:nth-child(4)::before {
  background-image: url("../img/features-ecology-4.png");
}

.features__text {
  margin: 0;
}
@media (min-width: 768px) {
  .features__text {
    text-align: left;
  }
}
@media (min-width: 1440px) {
  .features__text {
    text-align: center;
  }
}

.catalog {
  padding: 50px 0 60px 0;
}
@media (min-width: 768px) {
  .catalog {
    padding: 80px 0 100px 0;
  }
}
@media (min-width: 1440px) {
  .catalog {
    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-template-rows: repeat(6, auto);
    padding-top: 101px;
    padding-bottom: 120px;
  }
}

.catalog__badge {
  margin-bottom: 7px;
}
@media (min-width: 768px) {
  .catalog__badge {
    margin-bottom: 5px;
  }
}
@media (min-width: 1440px) {
  .catalog__badge {
    grid-column: 1/-1;
  }
}

.catalog__title {
  margin-bottom: 20px;
}
@media (min-width: 1440px) {
  .catalog__title {
    grid-column: 1/-1;
    margin-bottom: 36px;
  }
}

@media (min-width: 768px) {
  .catalog__filter-form {
    margin-bottom: 40px;
  }
}
@media (min-width: 1440px) {
  .catalog__filter-form {
    grid-row: 3/-1;
    margin-right: 20px;
    padding-top: 5px;
  }
}

.catalog__list {
  margin-bottom: 40px;
}
@media (min-width: 768px) {
  .catalog__list {
    margin-bottom: 60px;
  }
}
@media (min-width: 1440px) {
  .catalog__list {
    padding-left: 6px;
  }
}

.catalog__sort-form {
  padding: 35px 0 15px;
}
@media (min-width: 768px) {
  .catalog__sort-form {
    margin-left: auto;
    padding: 0 0 24px;
  }
}

@media (min-width: 768px) {
  .filter-form {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 30px;
    row-gap: 40px;
  }
}
@media (min-width: 1440px) {
  .filter-form {
    display: block;
  }
}

.filter-form__fieldset {
  margin: 0 0 25px 0;
  padding: 0;
  border: none;
}
@media (min-width: 768px) {
  .filter-form__fieldset {
    margin-bottom: 0;
  }
}
@media (min-width: 1440px) {
  .filter-form__fieldset {
    margin-bottom: 31px;
  }
}

.filter-form__range {
  margin: 0 0 21px 0;
}

.filter-form__price-inputs {
