.range {
  position: relative;
  width: 100%;
  height: 26px;
}

.range__scale {
  position: absolute;
  width: 100%;
  height: 2px;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background-color: #e2e2e2;
}
.range__scale--disabled div {
  pointer-events: none;
  background-color: #bdbdbd;
}
