  border: 2px solid #bdbdbd;
  background-color: #f8f8f8;
}

.range__bar {
  position: absolute;
  width: 93%;
  height: 4px;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background-color: #9070ec;
}

.range__toggle {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 24px;
  height: 24px;
  border-radius: 50%;
  border: 2px solid #9070ec;
  background-color: #ffffff;
  cursor: pointer;
}
.range__toggle--max {
  position: absolute;
  top: 50%;
  left: auto;
  right: 0;
  transform: translateY(-50%);
}
.range__toggle:hover::before, .range__toggle:focus::before {
  content: "";
  position: absolute;
  left: 2px;
  top: 50%;
  transform: translateY(-50%);
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: rgba(120, 89, 207, 0.25);
}
