.radio-control {
  position: relative;
  width: 100%;
}

.radio-control__input + label {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  margin-left: 34px;
}
.radio-control__input + label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 24px;
  height: 24px;
  background-color: #ffffff;
  border: 2px solid #bdbdbd;
  border-radius: 50%;
}
.radio-control__input + label::after {
  position: absolute;
  left: 4px;
  top: 50%;
  transform: translateY(-50%);
  width: 16px;
  height: 16px;
  background-color: rgba(120, 89, 207, 0.25);
  border-radius: 50%;
}
.radio-control__input + label:hover::after, .radio-control__input + label:focus::after {
  content: "";
}

.radio-control__input:checked + label::before {
  border: 2px solid #7859cf;
}
.radio-control__input:checked + label::after {
  content: "";
  background-color: #7859cf;
}

.radio-control__input:disabled + label {
  color: #bdbdbd;
  pointer-events: none;
}
.radio-control__input:disabled + label::before {
  background-color: transparent;
  border: 2px solid #e2e2e2;
}
.radio-control__input:disabled + label::after {
  content: none;
}

.radio-control__input:disabled:checked + label {
  color: #bdbdbd;
  pointer-events: none;
}
.radio-control__input:disabled:checked + label::before {
  border: 2px solid #e2e2e2;
}
.radio-control__input:disabled:checked + label::after {
  content: "";
  background-color: #e2e2e2;
}

.checkbox-control {
  position: relative;
  width: 100%;
}

.checkbox-control__input + label {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  margin-left: 52px;
}
.checkbox-control__input + label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 42px;
  height: 24px;
  background-color: #bdbdbd;
  border-radius: 12px;
}
.checkbox-control__input + label::after {
  content: "";
  position: absolute;
  left: 2px;
  top: 50%;
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
  background-color: #ffffff;
  border-radius: 50%;
}
.checkbox-control__input + label:hover::before, .checkbox-control__input + label:focus::before {
  border: 2px solid #9070ec;
}

.checkbox-control__input:checked + label::before {
  background-color: #7859cf;
  border: 2px solid #7859cf;
}
.checkbox-control__input:checked + label::after {
  left: 20px;
}

.checkbox-control__input:disabled + label {
  color: #bdbdbd;
  pointer-events: none;
}
.checkbox-control__input:disabled + label::before {
  background-color: #e2e2e2;
}

.checkbox-control__input:disabled:checked + label {
